<template>
  <div>
    <h2>15 методов работы с массивами в JavaScript, которые необходимо знать в 2020 году</h2>
    <p>Массив в JavaScript — это особая структура данных, которая используется для хранения различных элементов. С помощью встроенных в нее свойств и методов можно добавить, удалить, перебрать или обработать данные в соответствии со своими потребностями. Знание методов работы с массивами в JavaScript поднимет ваши профессиональные навыки в сфере разработки на новый уровень.</p>
    <p>В этой статье мы рассмотрим 15 методов, которые помогут вам более эффективно работать с массивами в JavaScript.</p>
    <p>Обратите внимание, что в основном мы будем упрощать функцию, которая передается в качестве параметра.</p>
    <code>
      myAwesomeArray.some(test => {<br>
      if (test === "d") {<br>
      return test<br>
      }<br>
      })<br>
      // We'll use the shorter one<br>
      myAwesomeArray.some(test => test === "d")<br>
    </code>
    <h3>1. some()</h3>
    <p>Этот метод проверяет, удовлетворяет ли какой-либо элемент массива условию, заданному в передаваемой функции. Он вернет значение true, если хотя бы один элемент совпадет с проверяемой функцией, и значение false — если нет.</p>
    <code>
      const myAwesomeArray = ["a", "b", "c", "d", "e"]<br>
      myAwesomeArray.some(test => test === "d")<br>
      //-------> Output : true<br>
    </code>
    <h3>2. reduce()</h3>
    <p>Этот метод принимает функцию, которая имеет в качестве аргумента аккумулятор и значение. Он применяет функцию к аккумулятору и каждому значению массива, чтобы в результате вернуть только одно значение.</p>
    <code>
      const myAwesomeArray = [1, 2, 3, 4, 5]<br>
      myAwesomeArray.reduce((total, value) => total * value)<br>
      // 1 * 2 * 3 * 4 * 5<br>
      //-------> Output = 120<br>
    </code>
    <h3>3. every()</h3>
    <p>Этот метод проверяет, удовлетворяют ли все элементы массива условию, заданному в передаваемой функции. Он вернет значение true, если каждый элемент совпадет с проверяемой функцией, и значение false — если нет.</p>
    <p></p>
    <code>
      const myAwesomeArray = ["a", "b", "c", "d", "e"]<br>
      myAwesomeArray.every(test => test === "d")<br>
      //-------> Output : false<br>
      const myAwesomeArray2 = ["a", "a", "a", "a", "a"]<br>
      myAwesomeArray2.every(test => test === "a")<br>
      //-------> Output : true<br>
    </code>
    <h3>4. map()</h3>
    <p>Этот метод принимает функцию в качестве параметра и создает новый массив с результатом вызова указанной функции для каждого элемента массива. Он всегда будет возвращать одинаковое количество элементов.</p>
    <code>
      const myAwesomeArray = [5, 4, 3, 2, 1]<br>
      myAwesomeArray.map(x => x * x)<br>
      //-------> Output : 25<br>
      // 16<br>
      // 9<br>
      // 4<br>
      // 1<br>
    </code>
    <h3>5. flat()</h3>
    <p>Этот метод принимает в качестве аргумента массив массивов и сглаживает вложенные массивы в массив верхнего уровня. Обратите внимание, что этот метод работает только для одного уровня.</p>
    <code>
      const myAwesomeArray = [[1, 2], [3, 4], 5]<br>
      myAwesomeArray.flat()<br>
      //-------> Output : [1, 2, 3, 4, 5]<br>
    </code>
    <h3>6. filter()</h3>
    <p>Этот метод принимает функцию в качестве параметра и возвращает новый массив, содержащий все элементы массива, для которого функция фильтрации передавалась в качестве аргумента, и возвращает ее со значением true.</p>
    <code>
      const myAwesomeArray = [<br>
      { id: 1, name: "john" },<br>
      { id: 2, name: "Ali" },<br>
      { id: 3, name: "Mass" },<br>
      { id: 4, name: "Mass" },<br>
      ]<br>
      myAwesomeArray.filter(element => element.name === "Mass")<br>
      //-------> Output : 0:{id: 3, name: "Mass"},<br>
      // 1:{id: 4, name: "Mass"}<br>
    </code>
    <h3>7. forEach()</h3>
    <p>Этот метод применяет функцию к каждому элементу массива.</p>
    <code>
      const myAwesomeArray = [<br>
      { id: 1, name: "john" },<br>
      { id: 2, name: "Ali" },<br>
      { id: 3, name: "Mass" },<br>
      ]<br>
      myAwesomeArray.forEach(element => console.log(element.name))<br>
      //-------> Output : john<br>
      // Ali<br>
      // Mass<br>
    </code>
    <h3>8. findIndex()</h3>
    <p>Этот метод принимает функцию в качестве параметра и в дальнейшем применяет ее к массиву. Он возвращает индекс найденного элемента, если элемент удовлетворяет условию проверяющей функции, переданной в качестве аргумента. Если не удовлетворяет, возвращается –1.</p>
    <code>
      const myAwesomeArray = [<br>
      { id: 1, name: "john" },<br>
      { id: 2, name: "Ali" },<br>
      { id: 3, name: "Mass" },<br>
      ]<br>
      myAwesomeArray.findIndex(element => element.id === 3)<br>
      //-------> Output : 2<br>
      myAwesomeArray.findIndex(element => element.id === 7)<br>
      //-------> Output : -1<br>
    </code>
    <h3>9. find()</h3>
    <p>Этот метод принимает функцию в качестве аргумента и в дальнейшем применяет ее к массиву. Он возвращает значение элемента, найденного в массиве, если элемент удовлетворяет условию проверяющей функции. В противном случае оно возвращается со значением undefined.</p>
    <code>
      const myAwesomeArray = [<br>
      { id: 1, name: "john" },<br>
      { id: 2, name: "Ali" },<br>
      { id: 3, name: "Mass" },<br>
      ]<br>
      myAwesomeArray.find(element => element.id === 3)<br>
      //-------> Output : {id: 3, name: "Mass"}<br>
      myAwesomeArray.find(element => element.id === 7)<br>
      //-------> Output : undefined<br>
    </code>
    <h3>10. sort()</h3>
    <p>Этот метод принимает функцию в качестве параметра. Он сортирует элементы массива и возвращает их.</p>
    <code>
      const myAwesomeArray = [5, 4, 3, 2, 1]<br>
      // Sort from smallest to largest<br>
      myAwesomeArray.sort((a, b) => a - b)<br>
      //-------> Output : [1, 2, 3, 4, 5]<br>
      // Sort from largest to smallest<br>
      myAwesomeArray.sort((a, b) => b - a)<br>
      //-------> Output : [5, 4, 3, 2, 1]<br>
    </code>
    <h3>11. concat()</h3>
    <p>Этот метод объединяет два или более массива/значения и возвращает новый массив.</p>
    <code>
      const myAwesomeArray = [1, 2, 3, 4, 5]<br>
      const myAwesomeArray2 = [10, 20, 30, 40, 50]<br>
      myAwesomeArray.concat(myAwesomeArray2)<br>
      //-------> Output : [1, 2, 3, 4, 5, 10, 20, 30, 40, 50]<br>
    </code>
    <h3>12. fill()</h3>
    <p>Этот метод заполняет все элементы массива одинаковым значением, от начального индекса (по умолчанию 0) до конечного индекса (по умолчанию array.length).</p>
    <code>
      const myAwesomeArray = [1, 2, 3, 4, 5]<br>
      // The first argument (0) is the value<br>
      // The second argument (1) is the starting index<br>
      // The third argument (3) is the ending index<br>
      myAwesomeArray.fill(0, 1, 3)<br>
      //-------> Output : [1, 0, 0, 4, 5]<br>
    </code>
    <h3>13. includes()</h3>
    <p>Этот метод возвращает значение true, если массив содержит определенный элемент, и значение false — если нет.</p>
    <code>
      const myAwesomeArray = [1, 2, 3, 4, 5]<br>
      myAwesomeArray.includes(3)<br>
      //-------> Output : true<br>
      myAwesomeArray.includes(8)<br>
      //-------> Output : false<br>
    </code>
    <h3>14. reverse()</h3>
    <p>Этот метод меняет порядок следования элементов в массиве на обратный. Первый элемент становится последним, а последний — первым.</p>
    <code>
      const myAwesomeArray = ["e", "d", "c", "b", "a"]<br>
      myAwesomeArray.reverse()<br>
      //-------> Output : ['a', 'b', 'c', 'd', 'e']<br>
    </code>
    <h3>15. flatMap()</h3>
    <p>Этот метод применяет функцию к каждому элементу массива, а затем сглаживает результат в новый массив. Он объединяет метод flat() и метод map() в одну функцию.</p>
    <code>
      const myAwesomeArray = [[1], [2], [3], [4], [5]] <br>
      myAwesomeArray.flatMap(arr => arr * 10)<br>
      //-------> Output : [10, 20, 30, 40, 50]<br>
      // With .flat() and .map()<br>
      myAwesomeArray.flat().map(arr => arr * 10)<br>
      //-------> Output : [10, 20, 30, 40, 50]<br>
    </code>
  </div>
</template>

<script>
export default {};
</script>

